import * as turf from '@turf/turf'
import Map from '../../components/Map'
const state = {
  drawing: false,
  polyArea: null,
  hexagonNames: null,
  userPolygon: null,
  ecoregions:null,
  polygonSW: null,
  polygonNE: null,
  payloadForIntersection: {},
  metricsToAssess : [],
  allMetrics: { //this object is used to populate the template
    "Hexagon ID" : "GRID_ID",
    "More Data" : "MoreData",
    "One Fish" : "1Fish",
    "Two Fish" : "2Fish",
    "Red Fish" : "RedFish",
    "Blue Fish" : "BlueFish"
  }
};

const getters = {
  isDrawing: state => state.drawing,
  polygonArea: state => state.polyArea,
  hexagonsInBox: state => state.hexagonNames,
  ecoregionsInBox: state => state.ecoregions,
  metricsToAssess: state => state.metricsToAssess,
  allMetrics: state => state.allMetrics,
  userPolygon: state => state.userPolygon
};

const actions = {
  toggleDrawing: ({ commit }) => { //commit is a given function that's used to call mutations
    commit('setDrawing', !state.drawing);  //toggle the Boolean
  },

  updatePolyArea: ({ commit }, payload) => {
    commit('updateArea', payload.polygon);
    commit('getDrawnPolyBbox', payload);
    commit('getHexagons', payload);
    commit('intersectAndParse', payload);
  },

  deleteUserPolygon: ( { commit }, payload) => {
    commit('clearDrawnPoly', payload);
  },

  updateMetricsToAssess: ({ commit }, payload) => {
    commit('updateMetricsArray', payload);
  }

};

const mutations = {

  updateMetricsArray: (state, payload) => {
    state.metricsToAssess = payload;
  },

  setDrawing: (state, drawing) =>{
    state.drawing = drawing;
  },

  clearDrawnPoly: (state, payload) =>{
    payload.draw.deleteAll();
    state.userPolygon = null;
    state.polyArea = null;
    state.ecoregions = null;
    state.hexagonNames = null;
  },

  updateArea: (state, poly) =>{
    // state.userPolygon = poly; //I thought I needed this, but it was causing the map background focus error, and everything works without it.
    var ref = this
    var drawnPolygon = poly.features[0];
    var polygonId = drawnPolygon.id.toString();
    var area = turf.area(drawnPolygon);
    var km2 = Math.round((area/1000000)*100)/100
    km2 = km2.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    state.polyArea  = km2;
  },

  getDrawnPolyBbox: (state, payload) => {
    var poly = payload.polygon;
    var map = payload.map;
    var ref = this
    var drawnPoly = poly.features[0];
    var polygonCenter = turf.center(drawnPoly).geometry.coordinates;
    var polygonBbox = turf.bbox(drawnPoly);
    var polygonSW = map.project([polygonBbox[0], polygonBbox[1]]);
    var polygonNE = map.project([polygonBbox[2], polygonBbox[3]]);
    state.polygonNE = polygonNE;
    state.polygonSW = polygonSW;

  },

  getHexagons: (state, payload) => {
    var drawnPoly = payload.polygon.features[0];
    state.payloadForIntersection.id = drawnPoly.id;
    state.payloadForIntersection.drawnPolygon = drawnPoly;
    state.payloadForIntersection.layerName = "hexagons"
    state.payloadForIntersection.hexagons = JSON.parse(JSON.stringify(payload.map.queryRenderedFeatures([[state.polygonSW.x, state.polygonSW.y], [state.polygonNE.x, state.polygonNE.y]], {layers: ['hexagons-webmerc']})))
  },

  intersectAndParse: (state, payload) =>{
    var drawnPoly = payload.polygon.features[0];
    var hexagons = state.payloadForIntersection.hexagons

    var hexagonIDs = []
    var ecoregions = []
    for (var i=0; i < hexagons.length; i++){
        if (turf.intersect(hexagons[i], drawnPoly)){
          for (var j=0; j < state.metricsToAssess.length; j++){
            if (state.metricsToAssess[j] === "GRID_ID"){
              hexagonIDs.push(hexagons[i].properties[state.metricsToAssess[j]]);
            }
          }
          ecoregions.push(hexagons[i].properties.ECOREG);
        }
    }
    var prettyNames = hexagonIDs.join(', ');
    ecoregions = [...new Set(ecoregions)]
    var prettyEcoregions = ecoregions.join(', ');
    state.ecoregions = prettyEcoregions
    state.hexagonNames = prettyNames;
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};
