<template>
  <v-card
    class="mx-auto mt-4"
    max-width="500"
  >
  <v-system-bar color="accent lighten-1">
    <v-spacer></v-spacer>
  </v-system-bar>
        <v-card class="pa-md-4">
          <p>This is where you'll go to start an analysis.  You'll click the sketch button and then start drawing.  This text will tell you what to do.</p>
          <div align="center">
            <template>
              <div class="text-right ma-4">
                <v-dialog
                  v-model="dialog"
                  width="500"
                >
                  <template v-slot:activator="{ on }" >
                    <v-btn v-on="on" text x-small>
                      <v-icon style="font-size:14px">mdi-settings</v-icon>
                      Select Metrics
                    </v-btn>
                  </template>

                  <v-card class="pa-4">
                    <v-card-title
                      class="headline secondary lighten-2"
                      primary-title
                    >
                      Select metrics for analysis
                    </v-card-title>
                    <v-container fluid >
                      <v-row justify="space-between">
                        <v-switch style="width: 130px;" v-model="metrics" v-for="(value, key) in allMetrics" :value="value" :label="key" :key="key" @change="updateAssessMetrics()" color="primary"></v-switch>
                      </v-row>
                    </v-container >
                    <v-btn @click="dialog = false">OK</v-btn>
                  </v-card>
                </v-dialog>
              </div>
            </template>

            <v-btn-toggle multiple raised>
              <v-btn color="accent" @click="callToggleDrawing"><v-icon color="white">mdi-pencil</v-icon>
                <span v-if="isDrawing" class="white--text">Clear</span>
                <span v-else>Sketch</span>
              </v-btn>
            </v-btn-toggle>
          </div>
        </v-card>
  </v-card>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';
  export default {
    name: 'SketchInputs',
    props: {
      source: String,
    },

    computed:{
       ...mapGetters(['isDrawing', 'metricsToAssess', "allMetrics", "userPolygon"]),
      },
    methods:{
      ...mapActions(['toggleDrawing', 'updateMetricsToAssess', 'updatePolyArea']),

      numMetrics: function(){
        var ref = this;
        return ref.metricsToAssess.length
      },

      callToggleDrawing: function(){
        this.toggleDrawing();
        this.updateAssessMetrics();
      },

      updateAssessMetrics: function(){
        var ref = this;
        ref.updateMetricsToAssess(ref.metrics);
      }
    },
    data: () => ({
      metrics : ["GRID_ID"], //this controls what being assessed by default
      dialog: false

    }),

  }
</script>
