<template>

      <div id="map"/>

</template>

<script>
  import mapboxgl from 'mapbox-gl';
  import { mapActions } from 'vuex'; //function that automatically connects vuex actions to components
  import { mapGetters } from 'vuex';
  import * as MapboxDraw from '@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw';
  import * as turf from '@turf/turf'
  import '@mapbox/mapbox-gl-draw/dist/mapbox-gl-draw.css'
  export default{
    name: 'Map',
    components: {

    },
    mounted: function() {
      this.measure();
      this.createMap();
      this.initDraw();
      this.drawFuncs(this.map);

    },

    props: {

    },

    computed:{
       ...mapGetters(['isDrawing']),
    },

    watch: {
      isDrawing(newValue, oldValue){
        this.toggleDrawMode(newValue);
      }
    },

    methods: {
      ...mapActions(['toggleDrawing', 'updatePolyArea', 'deleteUserPolygon']),

      measure(){
        var line = turf.lineString([[115, -32], [131, -22], [143, -25], [150, -34]]);
        console.log(line)
      },


      createMap() {
        mapboxgl.accessToken = 'pk.eyJ1IjoiZXJpa21hcnRpbiIsImEiOiJjancweTZ4dGowZzVsNGFsMjF6cmp0ZGk3In0.cwQYtCiplAs_a6XnqeUdpQ';
        this.map = new mapboxgl.Map({
            container: 'map', // container id
            style: 'mapbox://styles/erikmartin/ck5o3w5ot0onw1ijyewljclyb',
            center: [-70, 44], // starting position
            zoom: 8, // starting zoom,
            minZoom: 7,
            maxZoom: 18,
            hash: true
        });

        // Add scale control
        this.map.addControl(new mapboxgl.NavigationControl());
        var scale = new mapboxgl.ScaleControl({
            maxWidth: 120,
            unit: 'imperial'
        });
        this.map.addControl(scale, 'bottom-left');
        scale.setUnit('metric');
        return this.map
      },

      initDraw(){
        console.log("adding draw")
        this.Draw = new MapboxDraw({
          displayControlsDefault: false,
          controls: {
              polygon: false,
              line_string: false,
              combine_features: false,
              trash: false
          },
          userProperties: true,
        })
        this.map.addControl(this.Draw)
      },

      toggleDrawMode(activeDrawing) {
        var ref = this;
        if (activeDrawing===true){
          ref.Draw.changeMode('draw_polygon');
        }
        else{
          ref.Draw.changeMode('simple_select');
          var payload = {
            "draw" : ref.Draw
          }
          ref.deleteUserPolygon(payload)
        }
      },

      drawFuncs(map){
        var ref = this;
        map.on('draw.create', function (e) {
          var payload = {
            "polygon" : e,
            "map" : ref.map
          }
          ref.updatePolyArea(payload);
        });
        map.on('draw.update', function (e) {
          var payload = {
            "polygon" : e,
            "map" : ref.map
          }
          ref.updatePolyArea(payload);
        });
      }
    },
  }

</script>

<style>
  #map {
    position: absolute;
    top:64px;
    bottom:10px;
    left: 400px;
    width: calc(100% - 400px);
    height: calc(100% - 76px);
    margin: 0;
  }

</style>
